@tailwind base;
@tailwind components;
@tailwind utilities;

*,
*::after,
*::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: #969696 #474747;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
    width: 5px;
}

*::-webkit-scrollbar-track {
    background: #474747;
}

*::-webkit-scrollbar-thumb {
    background-color: #969696;
    border-radius: 10px;
    border: 3px solid #ffffff;
}
